@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  font-family: Roboto, sans-serif; }

.accueil-gutter {
  margin: 0 0 20px 0; }

.accueil-img {
  vertical-align: middle;
  border-style: none;
  max-width: 100%;
  height: auto; }

.ParallaxImg {
  display: inline-block;
  position: relative;
  overflow: hidden; }
  .ParallaxImg > img {
    vertical-align: middle;
    border-style: none;
    max-width: 100%;
    height: auto; }
  .ParallaxImg--effect:before {
    box-shadow: inset 0 0 20px 4px rgba(0, 0, 0, 0.5);
    position: absolute;
    z-index: 1;
    content: '';
    height: 100%;
    width: 100%; }

.user {
  padding: 10px; }
  .user > img {
    margin: 0 10px 0 0; }
  .user--empty {
    margin: 0 0 20px 0; }

.parallaxHeader {
  background-color: orange;
  box-sizing: border-box;
  position: relative;
  overflow: hidden; }
  .parallaxHeader__layer {
    background: linear-gradient(#ffc04d, #ffbc42);
    position: absolute;
    bottom: 0;
    height: 450px;
    width: 600px; }
    .parallaxHeader__layer--two {
      background: linear-gradient(#ffdb99, #ffd78f);
      height: 250px;
      width: 700px; }
    .parallaxHeader__layer--three {
      box-shadow: 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12), 0 11px 15px -7px rgba(0, 0, 0, 0.2);
      background: linear-gradient(#fff1d6, #ffedcc);
      height: 150px;
      width: 200px; }

